import React from 'react';

const Item = ({post, entity}) => (
    <article className="card">
        <a href={post.permalink}>
            <div className="thumbnail" style={{backgroundImage: `url(${post.thumbnail})`}} />
            <div className="card-content">
                <h3>{post.title}</h3>
            </div>
        </a>
    </article>
);

export default Item;
