import tippy from 'tippy.js';
import axios from 'axios';
import './wordlift-theme.css'

const contentParser = (json) => {
    let content = json[0],
        description = content.description;
    if(!content.image) {
        return description;
    }
    let image = content.image[0];
    if(image.width >= image.height){
        return `<img src="${image.url}" style="width: 100%; height: 200px; object-fit: cover;" />${description}`;
    }
    return `<img src="${image.url}" style="width: 50%; height: auto; float: right" />${description}<div style="clear: right"></div>`
}

export default (selector, baseUrl, tippyOptions = {}) => {

    let triggers = document.querySelectorAll(selector);

    let tippyDefaultOptions = {
        content: 'Loading...',
        animateFill: false,
        arrow: true,
        interactive: true,
        distance: 5,
        animation: 'fade',
        flipOnUpdate: true,
        theme: 'wordlift'
    }

    triggers.forEach(trigger => {
        tippy(trigger, {
            ...Object.assign(tippyDefaultOptions, tippyOptions),
            onShow: (instance) => {
                // We can monkey-patch the instance's state object with our own state
                if (instance.state.ajax === undefined) {
                    instance.state.ajax = {
                        isFetching: false,
                        canFetch: true,
                    }
                }

                // Now we will avoid initiating a new request unless the old one
                // finished (`isFetching`).
                // We also only want to initiate a request if the tooltip has been
                // reset back to Loading... (`canFetch`).
                if (instance.state.ajax.isFetching || !instance.state.ajax.canFetch) {
                    return
                }
                axios
                    .get(`${baseUrl}?entity_url=${trigger.getAttribute('href')}`)
                    .then(response => {
                        response.data && instance.setContent(contentParser(response.data));
                    })
                    .finally(() => {
                        // Make sure to set it back to false so new requests can be
                        // initiated
                        instance.state.ajax.isFetching = false
                    })
            },
            onHidden: (instance) => {
                instance.setContent('Loading...')
                instance.state.ajax.canFetch = true
            },
        })
    })

}

